/* HomePage.css */

.homepage {
  background: rgba(0, 0, 0, 1);
  top: 0;
  position: relative;
  color: white;
  width: 100vw;
  height: 100vh;
}

.background-image {
  position: fixed; /* Stays fixed during scroll */
  opacity: 80%;
  top: 0%;
  left: 76%;
  width: 21%;
  height: 100vh;
  background: url('images/typing.png') no-repeat center center; 
  background-size: cover; 
}
  
body, h1, h2, h3, h4, h5, h6 {
  font-family: 'Be Vietnam', sans-serif;
  /* overflow-x: hidden; */
}

.top-nav {
  font-weight: 600;
  font-size: 22px;
  position: sticky;
  top: 0;
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
  
.social-links {
  position: fixed;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  padding: 10px;
}
  
.social-links::before,
.social-links::after {
  content: '';
  position: absolute;
  left: 50%;
  width: 1px;
  background-color: white; /* Adjust the color as needed */
}

.social-links::before {
  top: -151%;
  height: 150%;
}

.social-links::after {
  bottom: -150%; /* Moves the line below the social links */
  height: 150%;
}

.content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

/* /////////// */

.fold{
  display: flex;
  padding-left: 19%;
  justify-content: right;
  align-items: center;
  height: 100%;
  max-width: 100vw;
  width: 100%; 
  
}

.background-black-fixed{
  /* width: 100vw; */
  overflow: hidden;
  height: 100%;
  overflow-y: auto;
  scroll-snap-type: y proximity;
  background-color: black;
  z-index: -1;
}
.fold, .container, section {
  height: 100vh; 
  scroll-snap-align: start; 
}

.container {
  display: flex;
  position: relative;
  z-index: 1;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  text-align: center;

}
/* SOFTWARE ENGINEER ect fade cycle */
@keyframes fadeCycle {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.15; }
}

/* Remove the span part from these selectors to apply the animation to the entire row */
.Line_Text:nth-child(1) {
  animation-delay: 0s;
}

.Line_Text:nth-child(2) {
  animation-delay: 3s; /* Starts at 30% opacity */
}

.Line_Text:nth-child(3) {
  animation-delay: 6s; /* Starts at 60% opacity */
}

.Line_Text {
  animation: fadeCycle 9s infinite;
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  font-weight: 800;
  font-size: 80px;
  gap: 30px; 
  align-items: center;
  width: 100%;
  justify-content: center; /* Ensure that the grid is centered in the container */
  margin-bottom: 20px; /* Space between the lines */
}

.Line_Text span:first-child {
  justify-self: end; /* Align the first word to the end of its grid cell */
}

.Line_Text span:nth-child(2) {
  justify-self: start; /* Align 'ENGINEER' to start, ensuring uniform position across lines */
  white-space: nowrap; /* Keep 'ENGINEER' on one line without breaking */
}

.Line_Text span:last-child {
  justify-self: start;
}

nav a {
  color: white;
  text-decoration: none;
  margin: 0 10px;
  z-index: 1;
}

aside a {
  display: block;
  color: white;
  margin-bottom: 10px;
}
/* 123 experience buttons */
.navigation-buttons{
margin-top: 5%;
}

.active_button {
  position: relative;
  overflow: hidden;
  display: inline-block;
  border-radius: 8px;
  padding: 12px 24px;
  outline: none;
  border: none;
  background: rgba(93, 253, 203, 0.9);
  color: black;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease, opacity 0.3s ease;
  text-align: center;
  width: 200px; /* Ensure width matches viewBox width for SVG */
  height: 50px; /* Ensure height matches viewBox height for SVG */
}

.active_button svg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.active_button rect {
  fill: none;
  stroke: #fff;
  stroke-width: 2;
  stroke-dasharray: 495; /* This should be the perimeter of the rectangle */
  stroke-dashoffset: 495; /* Initially equal to stroke-dasharray to hide stroke */
  transition: stroke-dashoffset 0.6s ease, stroke-width 0.3s ease;
}

.active_button:hover {
  color: white;
  animation-timing-function: ease-out;
  background-color: rgba(93, 253, 203, 0.0); /* Reduce background opacity on hover */
}

.active_button:hover rect {
  animation-timing-function: ease-out;
  stroke-dashoffset: 0;
}

