.box {
  position: relative;
  padding: 4.375rem 0 4.125rem 3.125rem;
}

/* Top border */
.box::before {
  content: '';
  position: absolute;
  background: white;
  height: 1px;
  width: 0;
  top: 0;
  left: 0;
  animation: drawTopBorder 1s ease forwards;
}

@keyframes drawTopBorder {
  from { width: 0; }
  to { width: 100%; }
}

/* Left border */
.box::after {
  content: '';
  position: absolute;
  background: white;
  width: 1px;
  height: 0;
  top: 0;
  left: 0;
  animation: drawLeftBorder 1s ease forwards 1s; /* Starts after the top border is done */
}

@keyframes drawLeftBorder {
  from { height: 0; }
  to { height: 100%; }
}

/* Bottom border */
.box .bottom-border {
  position: absolute;
  background: white;
  height: 1px;
  width: 0;
  bottom: 0;
  left: 0;
  animation: drawBottomBorder 1s ease forwards 2s; /* Starts after the left border is done */
}

@keyframes drawBottomBorder {
  from { width: 0; }
  to { width: 100%; }
}

